import React from 'react';
import Navigation from './components/Navigation/Navigation';
import Instruments from './components/Instruments/Instruments';
import Layers from './components/Layers/Layers';
import Canvas from './components/Canvas/Canvas';
import FrameViewer from './components/FrameViewer/FrameViewer';
import './mpindex.css';

export default class MainPage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {FieldSize: this.props.location.state.size,
                  UserName: this.props.location.state.name,
                  currentFPS: 1,
                  isPen: 'active',
                  isEraser: 'not-active',
                  isBucket: 'not-active',
                  ActiveInstrument: 'pen',
                  PrimaryColor: '#000000',
                  SecondaryColor: '#ffffff',
                  Layers: [
                    {Context: '',
                     Status: 'active',
                    }
                  ],
                  CurrentCanvas: 1,
                  CanvasContext: ''
                }
  }

  getFPS = (value) => {
    this.setState({currentFPS: value});
  }

  selectPen = () => {
    this.setState({isPen: 'active',
                   isEraser: 'not-active',
                   isBucket: 'not-active',
                   ActiveInstrument: 'pen'
                  })
  }
  
  selectBucket = () => {
    this.setState({isPen: 'not-active',
                   isEraser: 'not-active',
                   isBucket: 'active',
                   ActiveInstrument: 'bucket'
                  })
  }
  
  selectEraser = () => {
    this.setState({isPen: 'not-active',
                   isEraser: 'active',
                   isBucket: 'not-active',
                   ActiveInstrument: 'eraser'
                  })
  }

  getPrimaryColor = (primary) => {
    this.setState({PrimaryColor: primary})
  }

  getSecondaryColor = (secondary) => {
    this.setState({SecondaryColor: secondary})
  }

  addLayer = () => {
    this.state.Layers.forEach( (el) => { el.Status='not-active'; })
    let BufferArray = this.state.Layers;
    BufferArray.push({Context: '', Status: 'active'});
    this.setState({Layers: BufferArray,
                   CanvasContext: '',
                   CurrentCanvas: BufferArray.length});
  }

  selectLayer = (index) => {
    this.state.Layers.forEach( (el) => { el.Status='not-active' })
    let BufferArray = this.state.Layers;
    BufferArray[index-1] = ({Context: BufferArray[index-1].Context, 
                             Status: 'active'});
    this.setState({Layers: BufferArray,
                   CanvasContext: BufferArray[index-1].Context,
                   CurrentCanvas: index});
  }

  deleteLayer = (index) => {
    if (this.state.Layers.length !== 1) {
      let BufferArray = this.state.Layers;
      if(BufferArray[index-1].Status === 'active') {
        BufferArray[index-2] = ({Context: BufferArray[index-2].Context, 
                                 Status: 'active'});
        this.setState({CanvasContext: BufferArray[index-2].Context,
                       CurrentCanvas: index-1})
        }
      BufferArray.splice(index-1, 1);
      (this.state.CurrentCanvas >= index)? 
        this.setState({Layers: BufferArray,
                       CurrentCanvas: this.state.CurrentCanvas-1}):
        this.setState({Layers: BufferArray,
                       CurrentCanvas: this.state.CurrentCanvas-1});
    }
  }

  copyLayer = (index, info) => {
    let BufferArray = this.state.Layers;
    BufferArray.splice(index, 0, info);
    this.setState({Layers: BufferArray});
  }

  swapLayer = (index) => {
    if (this.state.Layers.length !== index) {
      let BufferArray = this.state.Layers;
      let BufferContainer = BufferArray[index-1];
      BufferArray[index-1] = BufferArray[index];
      BufferArray[index] = BufferContainer;
      this.setState({Layers: BufferArray});
    }
  }

  saveContext = (context, index) => {
    let BufferArray = this.state.Layers;
    BufferArray[index-1].Context = context;
    this.setState({Layers: BufferArray,
                   CanvasContext: context});
  }


  render() {
    return (
      <div>
        <Navigation name={this.state.UserName}/>
        <div class='main-page-container'>
          <Instruments 
            penDisplay={this.state.isPen}
            eraserDisplay={this.state.isEraser}
            bucketDisplay={this.state.isBucket}
            selectPen={this.selectPen}
            selectBucket={this.selectBucket}
            selectEraser={this.selectEraser}
            setPrimaryColor={this.getPrimaryColor}
            setSecondaryColor={this.getSecondaryColor}
          />
          <div  class='layers-container'>
            {this.state.Layers.map((el, index) => 
              <Layers 
                context={el.Context} 
                display={el.Status} 
                index={index+1}
                onPress={this.selectLayer}
                delete={this.deleteLayer}
                copy={this.copyLayer}
                swap={this.swapLayer}
              />)}
            <button class='new-layer' onClick={this.addLayer}></button>
          </div>
          <div class='drawing-field'>
              <Canvas
                fieldSize={this.state.FieldSize}
                instrument={this.state.ActiveInstrument}
                context={this.state.CanvasContext}
                primary={this.state.PrimaryColor}
                secondary={this.state.SecondaryColor}
                index={this.state.CurrentCanvas}
                saveContext={this.saveContext}
                />
          </div>
          <FrameViewer currentFPS={this.state.currentFPS} getCount={this.getFPS}/>
        </div>
      </div>
    );
  }
}