import React from 'react';
import Navigation from './components/Navigation/navigation';
import Intro from './components/Intro/intro';
import IntroImage from './components/IntroImage/introImage';
import ExampleText from './components/ExampleText/exampleText';
import Example from './components/IntroExample/introExample';
import AboutUsComponent from './components/AboutUsComponent/aboutUsComponent';
import Footer from './components/Footer/footer';
import LoginWindow from './components/LoginWindow/loginWindow';
import CreateWindow from './components/CreateWindow/createWindow';
import './spindex.css';

class Spindex extends React.Component {
  constructor(props) {
    super(props)
    this.state = {LoginDisplay: "display-false",
                  CreateDisplay: "display-false",
                  NameValue: "",
                  UserName: "",        
                  }
  }

  showLogin = () => {
    this.setState({LoginDisplay: "display-true-fixed"})
    console.log(this.props.deleting)
  }

  showCreate = () => {
    this.setState({CreateDisplay: "display-true-fixed"})
  }

  closeLogin = () => {
    let currentName = this.state.NameValue === ''? 'Guest': this.state.NameValue;
    this.setState({LoginDisplay: "display-false",
                    NameValue: currentName,
                    UserName: "Hello "+currentName
                  })
  }

  getName = (Name) => {
    this.setState({NameValue: Name.target.value})
  }

  render() {
    return (
      <div class = 'start-page'>
        <LoginWindow 
          display = {this.state.LoginDisplay} 
          onPress = {this.closeLogin} 
          enterLogin = {this.getName}
        />
        <CreateWindow 
          display = {this.state.CreateDisplay}
          name = {this.state.UserName? this.state.UserName: "Hello Guest"}
        />
        <Navigation 
          text={this.state.UserName} 
          onPressLogin = {this.showLogin} 
          onPressCreate = {this.showCreate} 
        />
        <div id = 'intro-container' class='intro-container'>
          <Intro 
            onPressLogin = {this.showLogin} 
            onPressCreate = {this.showCreate}
          />
          <IntroImage />
        </div>
        <div id = 'example-container' class = 'example-container'>
          <ExampleText />
          {userExamples.map(el => <Example text={el.text} image={el.image} />)}
        </div>
        <div id = 'aboutus-container' class = 'aboutus-container'>
          {aboutUs.map(el => <AboutUsComponent title={el.title} text={el.text} image={el.image} />)}
        </div>
          <Footer />
      </div>
    );
  }
}


const userExamples = [{text: 'Vodoo1', image: 'gif1.png'}, 
               {text: 'Vodoo2', image: 'gif1.png'}, 
               {text: 'Vodoo3', image: 'gif1.png'}, 
               {text: 'Vodoo4', image: 'gif1.png'},
               {text: 'Vodoo5', image: 'gif1.png'}]

const aboutUs = [{title: 'Title', text: 'Simple text', image: 'gif1.png'},
                 {title: 'Title', text: 'Simple text', image: 'gif1.png'},
                 {title: 'Title', text: 'Simple text', image: 'gif1.png'},
                 {title: 'Title', text: 'Simple text', image: 'gif1.png'},
                 {title: 'Title', text: 'Simple text', image: 'gif1.png'},
                 {title: 'Title', text: 'Simple text', image: 'gif1.png'},]

export default Spindex;