import React from 'react';

export default class Layers extends React.Component {

  constructor(props) {
    super(props)

    this.getContext = React.createRef();
  }

  componentDidUpdate() {
      this.getContext.current.style.background = `url(${this.props.context})`;
      this.getContext.current.style.backgroundSize = '100px 100px';
    }

  render() {
    return (
      <div>
        <div class={'canvas-container '+this.props.display} onClick={() => {this.props.onPress(this.props.index)} }>
          <div class='layer-number'>{this.props.index}</div>
          <div class='button-container' ref={this.getContext}>
            <button class='delete' onClick={(event) => {
              event.stopPropagation();
              this.props.delete(this.props.index)}}></button>
            <button class='copy' onClick={() => { this.props.copy( this.props.index, {Context: this.props.context, Status: this.props.display, CanvasStatus: 'active-canvas'} )}}></button>
            <button class='swap' onClick={(event) => {
              event.stopPropagation();
              this.props.swap(this.props.index)}}></button>
          </div>
        </div>
      </div>
    );
  }
}