import React from 'react';

let context;
let mouse = { x:0, y:0};
let draw = false;
let size;

export default class CanvasField extends React.Component {
  constructor (props) {
    super(props)

    this.getContext = React.createRef();
  }

  componentDidMount() {
    this.getContext.current.addEventListener('contextmenu', (e) => e.preventDefault())
    context = this.getContext.current.getContext('2d');
    size = 768/this.props.fieldSize;
  }

  componentWillReceiveProps(nextProps) {
    if (this.props.index !== nextProps.index) {
      context.clearRect(0, 0, 768, 768);
      
      if (nextProps.context !== '') {
        let img = new Image();
        img.src = nextProps.context;
        context.drawImage(img, 0,0)
      }
    }
  }

  mouseDown = (e) => {
    if (e.button === 0) {context.fillStyle = this.props.primary}
    if (e.button === 2) {context.fillStyle = this.props.secondary}            
    mouse.x = Math.ceil(e.nativeEvent.offsetX/size-1)*size;
    mouse.y = Math.ceil(e.nativeEvent.offsetY/size-1)*size;
    draw = true;
    context.beginPath();
    if (this.props.instrument === 'pen') context.fillRect(mouse.x, mouse.y, size, size);
    if (this.props.instrument === 'eraser') context.clearRect(mouse.x, mouse.y, size, size);
  };


  mouseMove = (e) => { 
      if(draw === true){
        mouse.x = Math.ceil(e.nativeEvent.offsetX/size-1)*size;
        mouse.y = Math.ceil(e.nativeEvent.offsetY/size-1)*size;
        if (this.props.instrument === 'pen') context.fillRect(mouse.x, mouse.y, size, size);
        if (this.props.instrument === 'eraser') context.clearRect(mouse.x, mouse.y, size, size);
      }
  };


  mouseUp = (e) => {
    mouse.x = Math.ceil(e.nativeEvent.offsetX/size-1)*size;
    mouse.y = Math.ceil(e.nativeEvent.offsetY/size-1)*size;
    context.fillRect(mouse.x, mouse.y, size, size);
    if (this.props.instrument === 'pen') context.fillRect(mouse.x, mouse.y, size, size);
    if (this.props.instrument === 'eraser') context.clearRect(mouse.x, mouse.y, size, size);
    draw = false;
  };


  render() {
    return (
      <div class={'canvas-field-container '+this.props.status}> 
        <canvas width='768' height='768'
          ref={this.getContext}
          onMouseDown={this.mouseDown}
          onMouseMove={this.mouseMove}
          onMouseUp={this.mouseUp}
          onClick = {() => this.props.saveContext(this.getContext.current.toDataURL('image/png'), this.props.index)}
        />
      </div>
    );
  }
}
