import React from 'react';
import history from '../../../history';

function CreateWindow(props) {
  return (
    <div class={'create-container '+ props.display}>
      <div class='create-window'>
        <p>Choose field size</p>
        <button onClick={() => history.push('/piskel', {size: 32, name: props.name})}>32*32</button>
        <button onClick={() => history.push('/piskel', {size: 64, name: props.name})}>64*64</button>
        <button onClick={() => history.push('/piskel', {size: 128, name: props.name})}>128*128</button>
      </div>
    </div>
  );
}

export default CreateWindow;