import React from 'react';

function Example(props) {
  const userName = props.text;
  const userGif = props.image;
  return (
    <div class = 'example-images'>
      <img src = {require(`../componentImages/${userGif}`)} alt = {userGif} width='116px' height='116px'></img>
      <p>{userName}</p>
    </div>
  );
}

export default Example;