import React from 'react'

function Navigation(props) {
    return (
        <div id = 'navigation-bar' class = 'nav-bar'>
            <a href='/#'>Piskel</a>
            <button id='faq-button'>FAQ</button>
            <button id='create-sprite' onClick={props.onPressCreate}>Create sprite</button>
            <button id='signin-button' onClick={props.onPressLogin}>Sign In</button>
            <a href='/#'>{props.text}</a>
        </div>
    );
}

export default Navigation;