import React from 'react';
import Slider from 'react-input-slider';

export default class FrameViewer extends React.Component {
  constructor(props) {
    super(props);
    
    this.state = {
      CurrentValue: this.props.currentFPS
    }
  }

  render() {
    return (
      <div class='viewer-container'>
        <div class='viewer'>Your example here</div>
        <p class='fps-counter'>{this.state.CurrentValue + " FPS"}</p>
        <div class='range-slider'>  
          <Slider
            axis='x'
            xstep={1}
            xmin={1} 
            xmax={30}
            x={this.state.CurrentValue}
            onChange={({x}) => {
              this.setState({CurrentValue: x})
              this.props.getCount(x);
              } 
            }
          />
        </div>
      </div>
    );
  }
  
}
