import React from 'react';
import InputColor from 'react-input-color';

function Instruments(props) {
    return (
        <div class='instruments-container'>
          <button class={'pen '+ props.penDisplay} onClick={props.selectPen}></button>
          <button class={'bucket '+ props.bucketDisplay} onClick={props.selectBucket}></button>
          <button class={'eraser '+ props.eraserDisplay} onClick={props.selectEraser}></button>
          <InputColor
            style = {{
              width: 50,
              height: 50,
              backgroundColor: '#444444',
              marginTop: 10,
              marginLeft: 5,
              marginBottom: 10
            }}
            initialHexColor='#000000'
            onChange={(value) => props.setPrimaryColor(value.hex)}
          />
          <InputColor
            style = {{
              width: 50,
              height: 50,
              backgroundColor: '#444444',
              marginLeft: 25,
              marginBottom: 10
            }}
            initialHexColor='#ffffff'
            onChange={(value) => props.setSecondaryColor(value.hex)}
          />
        </div>
    );
}

export default Instruments;