import React from 'react';

function LoginWindow(props) {

    return (
      <div class={'login-container '+ props.display}>
        <div class="login-window">
          <p>Enter your name</p>
          <input type="text" id="name-container" onChange={props.enterLogin}></input>
          <button id="submit-name" class="submit-name" onClick={props.onPress}>Submit</button>
        </div>
      </div>
    )
  }

export default LoginWindow;